.spin-octicon {
  -webkit-animation: spin-octicon 2s infinite linear;
          animation: spin-octicon 2s infinite linear;
}
@-webkit-keyframes spin-octicon {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(359deg);
  }
}
@keyframes spin-octicon {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(359deg);
  }
}
